.tooltip {
	background-color: black;
	color: white;
	text-align: center;
	padding: 5px;
	border-radius: 6px;
	position: absolute;
	top: -200%;
	width: 120px;
	left: calc(50% - 60px);
	opacity: 0;
	z-index: 1;
	transition: opacity 0.25s;
  }

.tooltip::after {
	content: " ";
	position: absolute;
	top: 100%; /* At the bottom of the tooltip */
	left: 50%;
	margin-left: -15px;
	border-width: 10px;
	border-style: solid;
	border-color: black transparent transparent transparent;
}
